@import url(https://fonts.googleapis.com/css?family=Krub:400,700|Open+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
  line-height: 22px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;

  background: #424242;
  color: white;
}

a {
  color: #FB5D86;
  cursor: pointer;
  font-weight: bold;

  text-decoration: none;
}

a:hover {
  font-weight: bold;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Krub', sans-serif;
  color: white;

  margin: 0;
  margin-bottom: 1rem;
}

h1 {
  font-size: 1.4rem;
  line-height: 1.6rem;
}

h2 {
  font-size: 1.2rem;
  line-height: 1.4rem;
}


h3 {
  font-size: 1.05rem;
  line-height: 1.1rem;
}

h4 {
  font-size: 1.05rem;
  line-height: 1.1rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

section {
  padding-bottom: 2rem;
}

.text-center {
  text-align: center;
}

.header {
  padding: 3.333rem 0;
  width: 18rem;

  margin: 0 auto;

  text-align: center;
}

.header .avatar {
  padding-bottom: 40px;
}

.header a.avatar {
  color: white;
}

.header .avatar img {
  display: inline-block;

  width: 30px;
  height: 30px;
  border-radius: 60px;
  border: 1px solid #979797;

  vertical-align: middle;

  margin-right: 1rem;
}

.header .avatar strong {
  display: inline-block;
}

.header ul {
  display: flex;
  flex-direction: row;

  justify-content: space-between;
  list-style: none;
}

.header ul li {
  font-family: 'Krub', sans-serif;
}

.header ul li a {
  padding-bottom: 0.2rem;
  padding-right: 0.5rem;
}
.header ul li a:hover {
  border-bottom: 1px solid #FB5D86;
}

.header ul li:after {
  font-family: 'Krub', sans-serif;

  color: white;
  content: '\2022';
  padding: 0;
  text-align: center;

  font-size: 1.3rem;
  line-height: 1.3rem;
  vertical-align: middle;
}

.header ul li:last-child:after {
  display: none;
}

.container {
  margin: 0 auto;
  width: 35rem;

  padding-bottom: 1.3333333rem;
}

@media only screen and (max-width : 36rem) {
  .container {
    padding: 0 1rem;
    width: auto;
  }
}

.App {
  text-align: justify;
}

.principle {
  margin-bottom: 2rem;
  border-bottom: 1px solid white;
}

.principle h3 {
  margin: 0;
  margin-top: 2rem;
}

.principle strong {
  border-bottom: 1px solid white;
}

.principle .work-in-progress {
  display: inline-block;
  padding: 1px 5px;
  margin-top: 5px;

  background: #FB5D86;
  color: white;

  font-size: 0.8rem;
  border-radius: 5px;
}

.App {
  text-align: justify;
}

